/* Adding gradient: https://cssgradient.io/ */
.App {
  text-align: center;
  position: fixed; 
  top: 0; 
  left: 0; 
  min-width: 100%;
  min-height: 100%;
  background-image: url(data:image/png;base64,);
  background-color: rgba(150, 147, 120, 0.6);

}

.virtual-window {
  position: absolute;
  top: 95px;
  left: 64px;
  border-style: solid;
  border-color: black;
  border-radius: 5px;
  width: 700px;
}

.top-bar {
  width: 100%;
  height: 20px;
  background-color: rgb(212, 250, 250);
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: black;
}

.plant {
  width:400px;
  height:800px;
  position: absolute;
  bottom: -190px;
  left:70px;
  z-index:-10;
  opacity:0.7;
}