.computer {
    width: 1250px;
    height: 750px;
    border: solid;
    border-color: rgb(162, 160, 160);
    background-image: url(data:image/png;base64,);
    background-color: rgb(0, 0, 0);
    border-width: 4px;
    position: fixed;
    bottom: 80px;
    right: 0;
    left: 0;
    margin: 0 auto;
    border-radius: 10px;
    z-index: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.screen {
    background: rgb(255,166,210);
    background: linear-gradient(180deg, rgba(255,166,210,0.7973783263305322) 20%, rgba(100,237,218,1) 54%, rgba(255,219,140,1) 89%);
    width: 96%;
    height: 92%;
    margin: 0 auto;
    overflow: hidden;
}

.stand {
    background-image: url(data:image/png;base64,);
    background-color: rgb(9, 8, 8);
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    margin: 0 auto;
    width: 300px;
    height: 300px;
    z-index: -1;
}

.webcam {
    position: relative;
    opacity: 0.14;
    pointer-events: none; 
    z-index:10000000000000;
}

.audio {
    position: absolute;
    left:530px;
    top:60px;
    height:135px;
    width: 318px;
    border: solid;
    border-color: black;
    background-color: rgb(245, 200, 200);
    border-radius: 5px;
}


.player {
    margin-top: 10px;
}

  
.song-box {
border-radius: 3px;
overflow: clip;
width: 88%;
height: 23px;
white-space: nowrap;
margin: auto;
text-overflow: clip;
}

.song-name {
overflow: wrap;
padding-top: 1px;
opacity: .9;
height: 20px;
font-weight: bold;
color: #926fb7;
animation: rightToLeft 15.5s linear infinite;
}

@keyframes rightToLeft {
0% {
    transform: translateX(300px);
}
100% {
    transform: translateX(-340px);
}
}

