.window {
    width: 600px;
    height: 400px;
    border: solid;
    border-color: rgb(142, 129, 129);
    border-width: 20px;
    position: absolute;
    right:-50px;
    top: -40px;
    background-image: url(data:image/png;base64,);
    background-color: rgb(3, 3, 35);
    z-index: -10;
}

.shades {
    width: 600px;
    height: 40px;
    background-color: rgb(198, 198, 212);
    border-bottom:rgb(142, 129, 129);
    border-bottom-width: 5px;
    border-bottom-style: solid;
}